/* --- 
----- SIDEBAR CONTAINER*/
.sidebar {
  position: relative;
  min-width: 40.7rem;
  height: 100%;
  background-color: #FFF4F4;
  -webkit-box-shadow: 12px 0px 0px rgba(255, 169, 169, 0.25);
          box-shadow: 12px 0px 0px rgba(255, 169, 169, 0.25);
  text-transform: uppercase;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  -webkit-transition: all .2s;
  -o-transition: all .2s;
  transition: all .2s;
  /*FULL SCREEN THIS BABY*/
  /*SIDEBAR TOGGLE*/ }
  @media (max-width: 75em) {
    .sidebar {
      min-width: 33rem; } }
  @media (max-width: 56.25em) {
    .sidebar {
      width: 0rem;
      min-width: 0rem; }
      .sidebar * {
        display: none; } }
  .sidebar--active {
    width: 95%;
    min-width: 95%;
    height: 95%;
    margin: auto;
    -webkit-box-shadow: 12px 12px 0px rgba(255, 169, 169, 0.25);
            box-shadow: 12px 12px 0px rgba(255, 169, 169, 0.25); }
    @media (max-width: 56.25em) {
      .sidebar--active {
        width: 100%;
        min-width: 100%;
        height: 100%;
        -webkit-box-shadow: 0 0 0 0;
                box-shadow: 0 0 0 0; }
        .sidebar--active * {
          display: block; } }
    .sidebar--active + .content-container {
      margin: 0;
      padding: 0;
      width: 0; }
  .sidebar__button {
    display: none; }
    @media (max-width: 56.25em) {
      .sidebar__button {
        display: block;
        position: absolute;
        top: 1rem;
        right: 2rem;
        height: 7rem;
        width: 7rem;
        border-radius: 50%;
        z-index: 2000;
        text-align: center;
        cursor: pointer;
        background-color: transparent; } }
  .sidebar__icon {
    display: none; }
    @media (max-width: 56.25em) {
      .sidebar__icon {
        display: inline;
        position: relative;
        margin-top: 3.5rem; }
        .sidebar__icon, .sidebar__icon::before, .sidebar__icon::after {
          width: 3rem;
          height: 2px;
          background-color: #999;
          display: inline-block; }
        .sidebar__icon::before, .sidebar__icon::after {
          content: "";
          position: absolute;
          left: 0;
          -webkit-transition: all .2s;
          -o-transition: all .2s;
          transition: all .2s; }
        .sidebar__icon::before {
          top: -.8rem; }
        .sidebar__icon::after {
          top: .8rem; }
        .sidebar__icon--active {
          background-color: transparent; }
          .sidebar__icon--active::before {
            top: 0;
            -webkit-transform: rotate(-135deg);
                -ms-transform: rotate(-135deg);
                    transform: rotate(-135deg); }
          .sidebar__icon--active::after {
            top: 0;
            -webkit-transform: rotate(135deg);
                -ms-transform: rotate(135deg);
                    transform: rotate(135deg); } }

/* --- 
----- LOGO CONTAINER*/
