/* --- 
----- LABELS CONTAINER*/
.labels {
  list-style-type: none;
  width: 40.7rem; }
  :not(.sidebar--active) > .labels {
    width: 100%; }
    :not(.sidebar--active) > .labels > .labels__item {
      width: 100%; }
  .labels__item {
    display: block;
    position: relative;
